const routes = [
  {
    path: "/",
    redirect: "/home",
  },
  {
    path: "/home",
    component: () =>
      import(/* webpackChunkName: "group-arab" */ "./views/TheHome.vue"),
  },
  {
    path: "/:notFound(.*)",
    component: () => import("./views/NotFound.vue"),
  },
];

export default routes;
