export default {
  setNumber(state, payload) {
    state.text.push(payload);
  },
  setLetter(state, payload) {
    state.text.push(payload);
  },
  deleteNumber(state) {
    state.text.pop();
  },
  space(state) {
    state.text.push(" ");
  },
  setArray(state, payload) {
    state.text = payload;
  },

  increaseFontSize(state) {
    state.increaseFontSize += 0.1;
  },
  decreaseFontSize(state) {
    state.increaseFontSize -= 0.1;
  },
  deleteAll(state) {
    state.text = [];
  },
  transliteration(state) {
    state.transliteration = !state.transliteration;
  },
  copy(state) {
    navigator.clipboard.writeText(state.text.join("")).then(
      function () {
        console.log("Async: Copying to clipboard was successful!");
      },
      function (err) {
        console.error("Async: Could not copy text: ", err);
      }
    );
  },
};
