export default {
  getNumber(state) {
    return state.text.join("");
  },
  getincreaseFontSize(state) {
    return state.increaseFontSize;
  },
  getdecreaseFontSize(state) {
    state.decreaseFontSize;
  },
  getselectAll(state) {
    state.selectAll;
  },
  getcopy(state) {
    state.copy;
  },

  getprev(state) {
    state.prev;
  },
  getnext(state) {
    state.next;
  },
  getTransliteration(state) {
    return state.transliteration;
  },
};
