export default {
  setNumber(context) {
    let num = event.target
      .closest(".numbers__item")
      .querySelector("span").textContent;
    context.commit("setNumber", num);
  },
  setLetter(context, payload) {
    context.commit("setLetter", payload);
  },
  async deleteNumber(context) {
    await context.commit("deleteNumber");
  },
  space(context) {
    context.commit("space");
  },
  setArray(context, payload) {
    context.commit("setArray", payload);
  },

  increaseFontSize(context) {
    context.commit("increaseFontSize");
  },
  decreaseFontSize(context) {
    context.commit("decreaseFontSize");
  },
  selectAll() {
    document.execCommand("selectall", null, false);
  },
  deleteAll(context) {
    context.commit("deleteAll");
  },
  copy(context) {
    context.commit("copy");
  },
  transliteration(context) {
    context.commit("transliteration");
  },
};
